import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import "./DesktopIcon.scss";

class DesktopIcon extends Component {
  constructor(props) {
    super(props);
    this.state = {
      top: `${this.props.position[0]}`,
      left: this.props.position[1],
      dragging: false,
    };
  }

  componentDidUpdate = prevProps => {
    if (prevProps.position !== this.props.position) {
      this.setState({
        top: `${this.props.position[0]}`,
        left: this.props.position[1]
      });
    }
  };

  dragOn = (iconName) => {
    this.setState({
      dragging: true,
    });
    window.addEventListener("mousemove", this.mouseMove);
    window.addEventListener("mouseup", this.dragOff);
  };

  mouseMove = (e) => {
    if (this.state.dragging) {
      this.setState({
        top: e.clientY - 50,
        left: e.clientX - 35,
      });
    }
  };

  dragOff = () => {
    this.setState({
      dragging: false,
    });
    window.removeEventListener("mousemove", this.mouseMove);
    window.removeEventListener("mouseup", this.dragOff);
  };

  render() {
    const { name, icon } = this.props;
    return (
      <React.Fragment>
        <div
          className="draggable-icon-container"
          style={{
            top: `${this.state.top}px`,
            left: `${this.state.left}px`,
          }}
          onMouseDown={() => this.dragOn()}
          onMouseUp={this.dragOff}
          onDoubleClick={this.props.maximize}>
          <div
            className="icon"
            style={{
              backgroundImage: `url(${icon})`,
            }}
          />
          <div className="name">{name}</div>
        </div>
        <div
          className="icon-mobile-container"
          style={{
            bottom: `${this.state.left - 15}px`,
            left: `20px`,
          }}
          onClick={this.props.maximize}>
          <img className="icon-mobile" src={icon} alt="icon" />
          <div className="name">{name}</div>
        </div>
      </React.Fragment>
    );
  }
}
export default withRouter(DesktopIcon);
